#navBar  {
    color: black;
    text-decoration: none;
    font-family: "Lato", sans-serif;
    height: 60px;
    width: 100%;
    z-index: 1;
    background-color: white;
    overflow-y: hidden;
    -ms-flex-pack: end;
        justify-content: flex-end;    
    padding-bottom: 1em;    
}

button {
    border-radius: 1em;
}

.topnav {
    height:60px;    
    -webkit-transition: 0.7s;    
    -o-transition: 0.7s;    
    transition: 0.7s;
    display: -ms-flexbox;
    display: flex; 
}

.navLinks {   
    margin-top: 10px;
    margin-right: 10px;
    text-align: right;
}

.topnav a:hover {
    color:grey;
}

.topnav h2 {
    -ms-flex-preferred-size: 100%;
        flex-basis: 100%;
}

a {
    color: black;
    font-size: 20px;
    text-decoration: none;
    font-family: "Roboto", sans-serif;
}

.hamburger {
    -ms-flex-item-align: start;
        align-self: flex-start;   
}

button:focus {outline:0;}

@media screen and (max-width: 767px) {
    .topnav h2 {
        font-size: 20px;
    }
}

@media screen and (min-width: 767px) {

    .hamburger, .navLinks {
        display: none;
    }

    .topnav {
        -ms-flex-preferred-size: 100%;
            flex-basis: 100%;
        margin-left: auto;
        margin-right: auto;
        -ms-flex-pack: justify;
            justify-content: space-between;
        
    }
}






