.cardText {      
    padding-top: 1em;
    padding-left: 1em;
    display: -ms-flexbox;
    display: flex; 
    -ms-flex-direction: row; 
        flex-direction: row;    
    -ms-flex-pack: distribute;    
        justify-content: space-around;
}

.detail {
    width: 100px;
    -ms-flex-item-align: center;
        align-self: center;
}

.shortdescription {
    display: none;
}

.title-large {
    display:none;
    
}

@media screen and (min-width:767px) {
    .title-large {
        display:initial;       
    }
    .title-small {
        display: none;
    }
    .cardText {
        -ms-flex-direction: column;
            flex-direction: column;
    }    
}

@media screen and (min-width:1200px) {
   
    .detail {
        display: -ms-flexbox;
        display: flex;
        width: 100%;
        -ms-flex-pack: center;
            justify-content: center;
        -ms-flex-align: center;
            align-items: center;
        
        -ms-flex-wrap: nowrap;
        
            flex-wrap: nowrap;
    }

    .detail div {
        margin-right: 1em;
    }


    .shortdescription {
        display: initial;
    }
}



